
let initialState = {
chatsinfo: [
{ id: 0, name: "Alina" },
{ id: 1, name: "Natalia" },
{ id: 2, name: "Sashko" },
{ id: 3, name: "Denis" },
],};
const chatsReducer =(state= initialState, action)=>{
    return {
        ...state,
        chatsinfo :[...state.chatsinfo]
    };
}

export default chatsReducer;